import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuController, AlertController } from '@ionic/angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { UserService } from '../services/user/user.service';
import { UserNewDTO } from '../models/user/user.new.dto';

@Component({
  selector: 'app-registrar',
  templateUrl: './registrar.page.html',
  styleUrls: ['./registrar.page.scss'],
})
export class RegistrarPage implements OnInit {

  formGroup: FormGroup;
  user: UserNewDTO;

  loading: boolean = false;

  constructor(private router: Router,
    private menu: MenuController,
    public formBuilder: FormBuilder,
    private userService: UserService,
    private alertCtrl: AlertController) {

    this.formGroup = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      apelido: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(15)]],
      senha: ['', [Validators.required, Validators.minLength(8)]],
      senhaR: ['', [Validators.required, Validators.minLength(8)]],
    });

  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.menu.enable(false)
  }

  registrar() {
    this.loading = true;
    this.user = {
      apelido: this.formGroup.get('apelido').value,
      email: this.formGroup.get('email').value,
      senha: this.formGroup.get('senha').value,
    }
    this.userService.insertNew(this.user).subscribe(res => {
      this.loading = false;
      this.showOk();
    },
      error => { this.loading = false });
  }

  async showOk() {
    let alert = await this.alertCtrl.create(
      {
        header: 'Sucesso!',
        message: 'Seu cadastro foi efetuado com sucesso!',
        backdropDismiss: false,
        buttons: [
          {
            text: 'OK',
            handler: () => { this.router.navigateByUrl('login'); }
          }
        ]
      }
    );

    alert.present();
  }

  getValid() {
    return !(this.formGroup.valid && this.getSenhas());
  }

  getSenhas(){
    return this.formGroup.get('senha').value == this.formGroup.get('senhaR').value;
  }
}
